@import url("https://fonts.googleapis.com/css2family=Bree+serif&family=Caveat:wght@500&display=swap");

*
{
  margin: 0;
  padding: 0;
}
body{
  background:url("http://su");
  font-family: "Bree serif",serif;
  display:flex;
  justify-content:center;
  align-items: center;
  min-height: 100vh;
}
.header{
  text-align: center;
  color: #f71aa2;
}
.app
{
  background-image: linear-gradient(to left bottom, #083705, #0d600f, #128b18, #14ba22, #12eb2d);
  width:95%;
  min-height: 200px;
  height: min-content;
  border-radius: 20px;
  padding:20px;
  box-shadow:10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}
.score-section{
  display: flex;
  align-items: center;
  font-size: 30px;
  color: white;
  font-weight: bold;
}
.question-section
{
  width:100%;
  position: relative;
}
question-count
{
  margin-bottom: 20px;
  font-size: 30px;
}
.question-text
{
  color: white;
  font-size: 30px;
  font-weight: bold;
}
.answer-section
{
  display: flex;
  flex-direction: column;
  width: 100%;
}
button{
  width: 100%;
  font-size: 16px;
  color:#ffffff;
  background-image: linear-gradient(to left bottom, #083705, #0d600f, #128b18, #14ba22, #12eb2d);
  border-radius: 15px;
  display: flex;
  padding: 10px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid #234668;
  cursor: pointer;
}
button:hover{
  background-color: #1b295c;
}